<template>
	<view class="page_forum" id="forum_details">
		<div_forum :obj="obj" style="background-color: #fff;" class="mb"></div_forum>

		<!-- 文章评论列表 -->
		<bar_title title="论坛评论"></bar_title>
		<list_comment style="background-color: #fff;" :list="list_comment"></list_comment>
		<!-- /文章评论列表 -->
		<!-- 发表评论 -->
		<view class="pa">
			<navigator style="background-color: #fff;" class="link" :url="'/pages/comment/edit?source_table=forum&source_field=forum_id&source_id=' + obj.forum_id">我来评论</navigator>
		</view>
		<!-- {{ obj }} -->
		<!-- {{ form }} -->
	</view>
</template>

<script>
	import bar_title from "../../components/diy/bar_title.vue";
	import div_forum from "../../components/diy/div_forum.vue";
	import list_comment from "../../components/diy/list_comment.vue";

	import mixin from "../../mixins/page.js";
	export default {
		mixins: [mixin],
		components: {
			bar_title,
			div_forum,
			list_comment
		},
		data() {
			return {
				url_get_obj: "~/api/forum/get_obj?",
				field: "forum_id",
				query: {
					forum_id: 0
				},
				obj: {},
				list_comment: [
				],
				form: {
					content: ""
				}
			}
		},
		methods: {
			onEditorReady() {
				const that = this
				uni.createSelectorQuery().select('#editor').context((res) => {

					this.editorCtx = res.context
					this.editorCtx.setContents({
						html: this.form.content,
						success: (res) => {
							console.log(res)
						},
						fail: (res) => {
							console.log(res)
						},
					})
				}).exec()
			},
			// 获取评论
			get_comment() {
				var options = getCurrentPages()[getCurrentPages().length - 1].options
				var query = {
					"source_table":"forum",
					"source_field":"forum_id",
					"source_id":options.forum_id
				}
				this.$get("~/api/comment/get_list?", query, (json) => {
					if (json.result) {
						var list_comment = json.result.list
						this.list_comment = list_comment
					}
				})
			},
			editorChange: function(e) {
				this.gongsijieshao = e.detail.html
			},
			/**
			 * 获取对象之后
			 * @param {Object} json 结果对象
			 */
			get_obj_after(json) {
				this.add_hits(json.result.obj)
			},
			/**
			 * 添加访问量
			 */
			add_hits(obj) {
				console.log("-------------------");
				this.$post('~/api/forum/set?forum_id=' + obj.forum_id, {
					hits: obj.hits + 1
				}, res => {
					obj.hits += 1
					console.log(res);
				})
			}
		},
		onLoad() {
			this.get_comment()
		}
	}
</script>

<style scoped>
	.page_forum {
		background: #f7f7f7;
		padding-bottom: 1rem;
	}

	.bar_title {
		background: none;
	}

	.recommend {
		margin: 0.5rem;
		background: #fff;
		border-radius: 0.5rem;
		overflow: hidden;
	}

	.link {
		text-align: center;
		padding: 0.5rem 0;
		border: 1px solid #DBDBDB;
		border-radius: 0.5rem;
	}
</style>
